/* header */
nav {
user-select: none;
}

nav li {
  display: inline-block;
  list-style: none;
  padding: 1vw 1.5vw;
}
nav li a {
  text-decoration: none;
  color: #7a7a7a;
}
nav li a:hover {
  color: #222222;
}
nav li .active {
  text-decoration: underline;
  color: #222222;
}
h2, 
h3, 
h4, 
h5 {
  font-weight: normal;
}

.logo {
  float: left;
  width: 3vw;
  height: auto;
  padding: 0.2vw 1.3vw;
  user-select: none;
}

/* recipe calc */ 

.recipe-list-item-container {
  padding: 1vw;
  position: relative;
  background-color: #d4d4d4;
  width: 30vw;
  margin: 1vw;  
  box-shadow: 0px 16px 16px 4px rgba(0, 0, 0, 0.12);  
  border: 0.1vw solid #d6d6d6;
  border-radius: 0.5vw;
  cursor: pointer;
}

.recipe-list-item-container a {
  text-decoration: none;
  color: #1a1a1a;
}

.list-container{
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: transparent;
  margin: auto;
  user-select: none;
}

.list-container > div {
  margin: 0 1vw 3vw;
}

.list-container > div:hover {
  border: 0.1vw solid #adadad;
  background-color: #dddddd;
}

.recipe-list-item img {
  border-radius: 0.6vw;
  height: 100%;
  object-fit: cover;
  width: 100%;
}

/* recipe */

.recipe-main-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: fit-content;  
  margin: auto;
}

.recipe-title-container {
  text-align: center;
  width: 100%;
  padding: 30px 0;
}

.recipe-main-image-container {
  margin: 0 1vw 1vw;
}

.recipe-main-image-container img {
  border-radius: 1vw;
  border: 0.2vw solid #a8a8a8;
  max-width: 90vw;
}

.ingredients-and-price-container {
  position: relative;
  display: flex;
  justify-content: center;
  background-color: #f1f1f1;
  outline: 2px solid #a8a8a8;
  border-radius: 1vw;
  margin: 0 0.5vw;
  overflow: hidden;
}

.ingredients-and-price-container h4 {
  text-align: center;
}

.ingredients-and-price-container > div {
  flex-shrink: 1;
  position: relative;
  padding: 0 0.5vw 0.5vw ;
  white-space: nowrap;
}

.ingredients-and-price-container > .ingredients-container {
  padding: 0 25px;
}

.ingredients-and-price-container > .ingredients-container h4 {
  margin-bottom:32px;
}

.ingredients-and-price-container > .portion-price-wrapper {
  background-color: #d3d3d3;
}

.ingredients-and-price-container > .package-price-wrapper{
  background-color: #c4c4c4;
}

.portion-price-wrapper h4,
.package-price-wrapper h4 {
  margin:12px auto 10px;
}

.portion-price {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: transparent;
}

.min-portion-price,
.average-portion-price {
  margin: 0 1vw;  
}

.package-price {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: transparent;
}

.min-package-price,
.average-package-price {
  margin: 0 1vw;
}

.portion-price h5,
.package-price h5 {
  margin: 0 auto;
  text-align: center;
}

.portion_selector_container {
  display: table;
  margin: 15px auto;
}

.portion_selector_container #portion_input {
  border-radius: 8px;
  padding: 3px 5px;
  border: 2px solid #e7e7e7;
  outline: none;
}

.portion_selector_container #portion_input:hover {
  border-color: #b6b6b6;
}

.portion_selector_container #portion_input:focus {
  border-color: #a0a0a0;
  background-color: #e0e0e0;;
}

.getPriceButton {
  display: table;
  margin: 15px auto;
}

/* products price calculator */

.product_inputs_container label {
  padding-top: 5px;
}

.product_inputs_container input {
  padding: 7px;
  margin: 7px;
  border-radius: 7px;
  border: 2px solid #adadad;
  outline: none;
}

.product_inputs_container input:hover {
  border-color: #9c9c9c;
}

.product_inputs_container input:focus {
  border-color: #9c9c9c;
  background-color: #ecebeb;;
}

.product_inputs > div {
  display: inline;
  margin: auto 15px;
}

button{
  border: 0;
  border-radius: 25px;
  background-color: #000000;
  padding: 10px 20px;
  margin: 0 5px;
  color: #fafafa;
  font-size: 10pt;
  font-weight: bold;
  transition:all 0.3s;
  user-select: none;
}

button:hover {
  background-color: #5f5f5f;
  cursor: pointer;
}

@media (min-width: 1300px) {
  .list-container {
    max-width: 70vw;
  }
}

@media (min-width: 701px) and (max-width: 970px) {
  .list-container > div {
    width: 45vw;
  }
  .product_inputs > div {
    display: block;
    width: 100%;
  }
}

@media (max-width: 700px) {
  .list-container > div {
    width: 80vw;
  }
  .product_inputs > div {
    display: block;
    width: 100%;
  }
}